<template>
<div class="rate">
  <div class="title"><span>{{comname}}</span></div>
  <ul>
    <li>
      <div class="left"></div>
      <el-rate v-model="promise" show-text></el-rate>
    </li>
    <li>
      <div class="left"></div>
      <el-rate v-model="serviceQuality" show-text></el-rate>
    </li>
    <li>
      <div class="left"></div>
      <el-rate v-model="repairQuality" show-text></el-rate>
    </li>
    <li>
      <div class="left"></div>
      <el-rate v-model="repairSpeed" show-text></el-rate>
    </li>
    <li>
      <div class="left"></div>
      <el-rate v-model="repairPrice" show-text></el-rate>
    </li>
  </ul>
  <div class="title"></div>
  <Input v-model="text" placeholder="?,~"
         type="textarea" :rows="6" :autosize="{ minRows: 6, maxRows: 6 }"></Input>
  <Button type="primary" class="submit" @click="submit"></Button>
</div>
</template>

<script>
	export default {
	  props:['comname','comid'],
		name: "rate",
    data(){
		  return{
        promise: 3,           // 
        serviceQuality: 3,    // 
        repairQuality: 3,     // 
        repairSpeed: 3,       // 
        repairPrice: 3,       // 
        text: '',
      }
    },
    methods:{
      submit(){
        let self= this
        if(!this.text.trim()){
          this.$Message.error('')
          return
        }
        let param = {
          accessToken: localStorage.getItem('ACCESSTOKEN'), // 
          companyId: this.comid,              // ID
          performance: this.promise*10,                        // 
          attitude: this.serviceQuality*10,                    // 
          quality: this.repairQuality*10,                      // 
          speed: this.repairSpeed*10,                          // 
          price: this.repairPrice*10,                          // 
          jsoninfo: {
            content: this.text,                          // 
            images: []                               // 
          },
          composite: Math.round(this.promise*6+this.serviceQuality+this.repairQuality*2+this.repairSpeed*0.5+this.repairPrice*0.5) //  
        }
        this.axios({
          method: 'post',
          url: '/company/review/submit',
          headers: {
            'Content-type': 'application/json'
          },
          data: JSON.stringify(param)
        })
          .then(res => {
            console.log(res);
            if(res.data.code=='120513'){
              self.$Message.error(',')
            }else if(res.data.code=='120509'){
              self.$Message.error(',')
            }else if(res.data.code=='120500'){
              self.$Message.success({content:'', duration:5})
              self.$emit('success')
            }else{
              self.$Message.error(res.data.status)

            }
          })
      }
    }
	}
</script>

<style scoped lang="scss">
.rate{
  width: 100%;
  .title{
    background: #f8f8f8;
    height: 40px;
    line-height: 40px;
    padding-left: 15px;
    span{
      color: #1E9FFF;
    }
  }
  ul{
    padding: 10px 0;
    li{
      padding: 4px 15px;
      text-align: center;
      .left{
        float: left;
        width: 150px;
        font-size: 14px;
        text-align: right;
      }
    }
  }
  .submit{
    float: right;
    margin: 10px;
  }
}
</style>



