<script>
export default {
  name: 'complain',
  data(){
    return {
      columns1: [
        {
          title: '',
          key: 'man'
        },
        {
          title: '',
          key: 'tel'
        },
        {
          title: '',
          key: 'companyName'
        },
        {
          title: '',
          key: 'companyAddress'
        }
      ],
      data1: []
    }
  }
}
</script>

<style lang='scss' scoped></style>



