<template>
<div>
<div class="topbar">
  <div v-if="!userName()" class="greet"><span></span>
    <router-link tag="a" :to="{ name: 'login'}"></router-link>|
    <router-link tag="a" :to="{ name: 'register'}"></router-link>
  </div>
  <div v-if="userName()" class="greet"><span></span><span>{{userName()}}</span>
    <router-link tag="span" :to="{ path: '/center/eCarList'}" v-if="userType()==1" class="button"></router-link>
    <router-link tag="span" :to="{ name: 'comHome'}" v-if="userType()==2" class="button"></router-link>
    <router-link tag="span" :to="{ name: 'manHome'}" v-if="userType()==3" class="button"></router-link>
    <a @click="logout"></a>
  </div>
</div>
<div class="midbar">
  <img class="logo" src="../assets/header/logo1.png">
  <div class="title">
    <p></p><p>Shanghai Automobile Maintenance public Service  Platform</p>
  </div>
  <div class="wx" @mouseover="showqr2" @mouseout="hideqr2">
    <img src="../assets/header/vx.png"><span></span>
  </div>
  <div class="app" @mouseover="showqr1" @mouseout="hideqr1">
    <img src="../assets/header/sj.png"><span>APP</span>
  </div>
  <img class="qrcode1" src="../assets/header/qr2.jpg" v-show="qr1" @mouseover="showqr1" @mouseout="hideqr1"/>
  <img class="qrcode2" src="../assets/header/qr2.jpg" v-show="qr2" @mouseover="showqr2" @mouseout="hideqr2"/>
</div>
<div class="comp">
  <div class="left"><img src="../assets/header/tlogo1.png"/><div></div></div>
  <div class="mid"><img src="../assets/header/tlogo2.png"/><div></div></div>
  <div class="right"><img src="../assets/header/logo1.png"/><div></div></div>
</div>

  <Menu mode="horizontal" theme="primary" :active-name="this.$route.path" @on-select="select">
    <MenuItem name="/index"></MenuItem>
    <MenuItem name="/public/articleList?id=10"></MenuItem>
    <MenuItem name="/center/eCarList"></MenuItem>
    <Submenu name="4">
      <template slot="title"></template>
      <MenuItem name="/maintain?type=maintain"></MenuItem>
      <MenuItem name="/service/carDoctor"></MenuItem>
      <MenuItem name="/service/visitingService"></MenuItem>
      <MenuItem name="/service/reservationFix"></MenuItem>
      <!--<MenuItem name=""></MenuItem>-->
      <MenuItem name="/maintain?type=check"></MenuItem>
      <MenuItem name="/maintain?type=danger"></MenuItem>
      <MenuItem name="/maintain?type=newenergy"></MenuItem>
      <MenuItem name="/maintain?type=rescue"></MenuItem>
    </Submenu>
    <MenuItem name="/association/articleList?id=18"></MenuItem>
    <Submenu name="6">
      <template slot="title"></template>
      <MenuItem name="/supervision/satisfaction"></MenuItem>
      <MenuItem name="/supervision/complaint"></MenuItem>
    </Submenu>
    <Submenu name="7">
      <template slot="title"></template>
      <MenuItem name="/business/fittings"></MenuItem>
      <MenuItem name="/business/equipment"></MenuItem>
      <MenuItem name="/business/insurance"></MenuItem>
      <MenuItem name="/business/sell"></MenuItem>
      <MenuItem name="/business/train"></MenuItem>
      <MenuItem name="/business/goods"></MenuItem>
      <MenuItem name="/business/books"></MenuItem>
      <MenuItem name="/business/beautify"></MenuItem>
      <MenuItem name="/business/check"></MenuItem>
      <MenuItem name="/business/study"></MenuItem>
      <MenuItem name="/business/help"></MenuItem>
      <MenuItem name="/business/service"></MenuItem>
    </Submenu>
    <MenuItem name="/about"></MenuItem>
  </Menu>
  <BackTop></BackTop>
</div>
</template>

<script>

export default {
  data(){
    return{
      qr1: false,
      qr2: false,
    }
  },
  computed:{

  },
  methods:{
    userName(){
      let name= this.$store.getters.userName, localInfo= localStorage.getItem('USERINFO')
      if (!name){
        if (localInfo){
          this.$store.commit("putUserInfo",JSON.parse(localInfo))
        }
      }
      return this.$store.getters.userName
    },
    userType(){
      return this.$store.getters.userType
    },
    logout(){
      let self= this
      this.$Modal.confirm({
        title: '',
        content: '',
        onOk: () => {
          this.$Message.success('');
          localStorage.removeItem("ACCESSTOKEN");
          localStorage.removeItem("USERINFO");
          self.$store.commit("putUserInfo",{})
          self.$router.replace({
            path: '/login',
          });
        },
        onCancel: () => {
//          this.$Message.info('Clicked cancel');
        }
      });
    },
    showqr1(){
      this.qr1=true
    },
    hideqr1(){
      this.qr1=false
    },
    showqr2(){
      this.qr2=true
    },
    hideqr2(){
      this.qr2=false
    },
    select(name){
      // console.log(name)
      if(name){
        this.$router.push({
          path: name
        })
      }

    }
  }

}
</script>

<style scoped lang="scss">
.topbar{
  width: 100%;
  min-width: 800px;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  background-color: #f2f2f2;
  overflow: hidden;
  .greet{
    float: right;
    margin-right: 10px;
    a{
      padding: 0 5px;
    }
    .button{
      background: #4ba7f5;
      color: white;
      padding: 2px 5px;
      line-height: 40px;
      text-align: center;
      cursor: pointer;
    }
  }
}
.midbar{
  margin-top: 20px;
  position: relative;
  min-width: 800px;
  .logo{
    width: 60px;
    margin: 0 20px;
  }
  .title{
    display: inline-block;
    p{
      font-size: 30px;
    }
    p:last-child{
      font-size: 15px;
      margin-top: 5px;
    }
  }
  .app,.wx{
    float: right;
    margin-right: 20px;
    font-size: 14px;
    height: 33px;
    overflow: hidden;
    margin-top: 10px;
    img{
      display: block;
      float: left;
    }
    span{
      display: block;
      float: left;
    }
  }
  .qrcode1{
    position: absolute;
    width: 160px;
    right: 110px;
    top: 30px;
    z-index: 901;
  }
  .qrcode2{
    position: absolute;
    width: 160px;
    right: 0;
    top: 30px;
    z-index: 901;
  }

}
.comp{
  width: 900px;
  height: 30px;
  margin: 10px auto;
  text-align: center;
  >div{
    width: 33%;
    float: left;
    height: 30px;
    line-height: 30px;
    font-size: 14px;
    img{
      float: left;
      width: 30px;
    }
    div{
      float: left;
      line-height: 30px;
    }
  }
  .mid{
    img{
      width: 35px;
    }
  }
}

.ivu-menu{
  min-width: 800px;
  height: 40px;
  display: -webkit-flex;
  display: flex;
  justify-content: center;
}
.ivu-menu li{
  height: 40px;
  line-height: 40px;
  padding: 0 18px;
}
</style>




