<script>
  export default {
    name: "reservationFix",
    data(){
      return {
        formItem: {
          name: '',
          tel: '',
          address: '',
          content: ''
        }
      }
    },
    methods: {
      Submit(){

      }
    }
  }
</script>

<style lang="scss" scoped>
  #reservationFix {
    width: 980px;
    min-height: 700px;
    margin: 10px auto;
    border: 1px solid #ddd;
    box-sizing: border-box;
    padding: 10px;
    .content {
      border: 1px solid #ddd;
      box-sizing: border-box;
      padding: 10px;
      margin-top: 10px;
      min-height: 620px;
    }
  }
</style>



