<template>
<div>
<Row>
  <div class="left-block" v-if="userType()==3">
    <div class="title"></div>
    <Menu theme="light" class="left-menu"
          :active-name="this.$route.path" @on-select="select" :open-names="['3','4','5']">
      <MenuItem name="/center/manHome"><Icon type="ios-paper"></Icon></MenuItem>
      <MenuItem name="/center/eCarList"><Icon type="ios-paper"></Icon></MenuItem>
      <Submenu name="3">
        <template slot="title"><Icon type="ios-paper"></Icon></template>
        <MenuItem name="/center/manNotify"></MenuItem>
        <MenuItem name="/center/notes"></MenuItem>
        <MenuItem name="/center/manComplain"></MenuItem>
        <MenuItem name="/center/manSurvey"></MenuItem>
        <MenuItem name="">
          <a style='color: #333' href="http://218.242.195.69:3320/hytjglxt/login.vm" target="_blank"></a>
        </MenuItem>
        <MenuItem name="/center/manDownload"></MenuItem>
      </Submenu>
      <Submenu name="4">
        <template slot="title"><Icon type="ios-people"></Icon></template>
        <MenuItem name="/center/manCompanyInfo"></MenuItem>
        <MenuItem name="/center/manQuality"></MenuItem>
      </Submenu>
      <Submenu name="5">
        <template slot="title"><Icon type="stats-bars"></Icon></template>
        <MenuItem name="/center/baseInfo"></MenuItem>
        <MenuItem name="/center/resetPassword"></MenuItem>
        <MenuItem name="exit"></MenuItem>
      </Submenu>
    </Menu>
  </div>

  <div class="left-block" v-if="userType()==2">
    <div class="title"></div>
    <Menu theme="light" class="left-menu"
          :active-name="this.$route.path" @on-select="select" :open-names="['3','4','5','6']">
      <MenuItem name="/center/comHome"><Icon type="ios-paper"></Icon></MenuItem>
      <MenuItem name="/center/eCarList"><Icon type="ios-paper"></Icon></MenuItem>
      <Submenu name="3">
        <template slot="title"><Icon type="ios-paper"></Icon></template>
        <MenuItem name="/center/comVisit"></MenuItem>
        <MenuItem name="/center/comAppoint"></MenuItem>
      </Submenu>
      <Submenu name="4">
        <template slot="title"><Icon type="ios-people"></Icon></template>
        <MenuItem name="/center/notes"></MenuItem>
        <MenuItem name="/center/comComplain"></MenuItem>
      </Submenu>
      <Submenu name="5">
        <template slot="title"><Icon type="ios-people"></Icon></template>
        <MenuItem name="/center/comUpload"></MenuItem>
        <MenuItem name=""><a style='color: #333' href="http://218.242.195.69:3320/hytjglxt/login.vm" target="_blank"></a></MenuItem>
      </Submenu>
      <Submenu name="6">
        <template slot="title"><Icon type="stats-bars"></Icon></template>
        <MenuItem name="/center/baseInfo"></MenuItem>
        <MenuItem name="/center/resetPassword"></MenuItem>
        <MenuItem name="exit"></MenuItem>
      </Submenu>
    </Menu>
  </div>

  <div class="left-block" v-if="userType()==1">
    <div class="title"></div>
    <Menu theme="light" class="left-menu"
          :active-name="this.$route.path" @on-select="select" >
      <MenuItem name="/center/eCarList"><Icon type="ios-paper"></Icon></MenuItem>
      <MenuItem name="/center/perBindcar"><Icon type="ios-paper"></Icon></MenuItem>
      <MenuItem name="/center/notes"><Icon type="ios-paper"></Icon></MenuItem>
      <MenuItem name="/center/perMyquestion"><Icon type="ios-paper"></Icon></MenuItem>
      <MenuItem name="/center/perMyvisit"><Icon type="ios-paper"></Icon></MenuItem>
      <MenuItem name="/center/perMyappoint"><Icon type="ios-paper"></Icon></MenuItem>
      <MenuItem name="/center/baseInfo"><Icon type="ios-paper"></Icon></MenuItem>
      <MenuItem name="/center/resetPassword"><Icon type="ios-paper"></Icon></MenuItem>
      <MenuItem name="exit"><Icon type="ios-paper"></Icon></MenuItem>

    </Menu>
  </div>

  <div class="right-block">
    <router-view/>
  </div>
</Row>
</div>
</template>

<script>
export default {
  data(){
    return{

    }
  },
  methods:{
    userType(){
      return this.$store.getters.userType
    },
    select(name){
      console.log(name)
      switch (name){
        case 'exit':{
          this.logout()
          break
        }
        default:
          this.$router.push({
            path: name
          })
      }

    },
    logout(){
      let self= this
      this.$Modal.confirm({
        title: '',
        content: '',
        onOk: () => {
          this.$Message.success('');
          localStorage.removeItem("ACCESSTOKEN");
          localStorage.removeItem("USERINFO");
          self.$store.commit("putUserInfo",{})
          self.$router.replace({
            path: '/login',
          });
        },
        onCancel: () => {
//          this.$Message.info('Clicked cancel');
        }
      });
    },
  }
}
</script>

<style scoped lang="scss">
.left-block{
  float: left;
  border-right: 1px solid #dddee1;
  border-bottom: 1px solid #dddee1;
  .title{
    width: 215px;
    height: 45px;
    background: url(../assets/leftmenu/banner.png) no-repeat;
    margin: 15px auto 0 auto;
    position: relative;
    color: #FFF;
    font-size: 16px;
    padding-left: 40px;
    line-height: 38px;
  }
  .title:after {
    content: "";
    width: 24px;
    height: 24px;
    position: absolute;
    top: 6px;
    left: 10px;
    background: url(../assets/leftmenu/book.png) no-repeat;
  }
  .left-menu{
    min-height: 520px;
  }
  .ivu-menu::-webkit-scrollbar{

  }
}
.right-block{
  margin: 10px 10px 10px 250px;
  padding: 0 10px;
  border: 1px solid #dddee1;
  min-height: 560px;
  min-width: 550px;
}

.ivu-menu-vertical.ivu-menu-light:after{
  background: none;
}
</style>



