<template>
  <div id="questionSearch">
    <div class="pro_wrap clearFix">
      <div class="pro_left">
        <sub-title title="" :link="[{name:'',to: 'carDoctor'},{name:'',to:''}]"></sub-title>
        <div class="search_box">
          <Input v-model="searchKeyWords" placeholder="" style="width: 400px"></Input><Button type="primary" icon="ios-search"></Button>
        </div>
        <div class="search_category">
          <span>:</span>
          <Select v-model="carBrand" size="small" style="width:100px">
            <Option v-for="item in carBrandList" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
          <span class="sc_pro_type">:</span>
          <Select v-model="troubleType" size="small" style="width:100px">
            <Option v-for="item in troubleTypeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </div>
        <Table border :columns="columns" :data="tableData"></Table>
      </div>
      <professor-list></professor-list>
    </div>
  </div>
</template>

<script>
import ProfessorList from "../../components/ProfessorList";

export default {
  components: {ProfessorList},
  name: "questionSearch",
  data(){
    return{
      searchKeyWords: '',  // 
      carBrand: '',        // 
      troubleType: '',      // 
      carBrandList: [
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        }
      ],
      troubleTypeList: [
        {
          value: '',
          label: '----'
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
      ],
      columns: [
        {
          title: '',
          key: 'problem',
          width: 350
        },
        {
          title: '',
          key: 'date',
          align: 'center',
          width: 100
        },
        {
          title: '',
          key: 'count',
          align: 'center'
        },
        {
          title: '',
          align: 'center',
          key: 'id',
          width: 100,
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                on: {
                  click: ()=> {
                    console.log(params.row.id)
                    this.$router.push({
                      path:'',
                      query:{id: params.row.id}
                    })
                  }
                }
              }, '')
            ])
          }
        }
      ],
      tableData: [
        {
          problem: '?',
          date: '2017-10-12',
          count: '0',
          id: 1
        },
        {
          problem: '?',
          date: '2017-10-12',
          count: '0',
          id: 2
        }
      ]
    }
  },
  beforeCreate(){

  },
  methods:{

  }
}
</script>

<style lang="scss" scoped>
  #questionSearch {
    .clearFix {
      &:after {
        content: '';
        clear: both;
        display: block;
        height: 0;
      }
      zoom: 1;
    }
    .pro_wrap {
      width: 980px;
      margin: 0 auto;
      .pro_left {
        width: 690px;
        min-height: 800px;
        border: 1px solid #d4d4d4;
        margin: 10px 0;
        float: left;
        padding: 10px;
        .search_box {
          width: 473px;
          margin: 20px auto;
        }
        .search_category {
          width: 420px;
          margin: 0 auto 30px;
          .sc_pro_type {
            margin-left: 30px;
          }
        }
      }
    }
  }
</style>

<style lang="scss">
  #questionSearch {
    .pro_left .search_box {
      input, button{
        border-radius: 0;
      }
    }
  }
</style>



