<template>
<div>
  <sub-title title="" :link="[{name:'',to: '/center/eRecordList'},{name:'',to:''}]"></sub-title>
  <Form  :label-width="80" style="width: 500px;overflow: hidden;margin: 10px 0">
    <FormItem label="">
      <Input v-model="carcard" placeholder=""></Input>
    </FormItem>
    <FormItem label="VIN">
      <Input v-model="vin" placeholder=""></Input>
    </FormItem>
    <FormItem label="">
      <Button type="primary" @click="bindCar" ></Button>
    </FormItem>
  </Form>
</div>
</template>

<script>
export default {
  name: "bindcar",
  data(){
    return{
      carcard:"",
      vin:""
    }
  },
  methods:{
    bindCar(){
      if(!this.carcard.trim()||!this.vin.trim()){
        this.$Message.error("")
        return
      }
      let self=this, param={
        "accessToken": localStorage.getItem('ACCESSTOKEN'),
        "vehicleplatenumber": this.carcard.trim(),
        "vin": this.vin.trim()
      }
      this.axios({
        method: 'post',
        url: '/vehicle/owner/bind',
        headers: {
          'Content-type': 'application/json'
        },
        data: JSON.stringify(param)
      }).then(function (res) {
        console.log(res)
        if(res.data.code='000000'){
          self.$Message.success(res.data.status)
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">

</style>



