<template>
<div>
  <sub-title title="" :link="[{name:'',to: '/center/manHome'},{name:'',to:''}]"></sub-title>
  <Form :model="formItem" :label-width="80" style="width: 500px;overflow: hidden;margin: 10px 0">
    <FormItem label="">
      <Input v-model="formItem.title" placeholder=""></Input>
    </FormItem>
    <FormItem label="">
      <Input v-model="formItem.context" type="textarea" placeholder="" class="textarea"></Input>
    </FormItem>
    <FormItem label="">
      <!--<Cascader :data="select" v-model="selectv" trigger="hover"></Cascader>-->
      <Select v-model="select1" style="width:100px;float: left;margin-right: 5px">
        <Option value="all" ></Option>
        <Option value="1" ></Option>
        <Option value="2" ></Option>
        <Option value="3" ></Option>
      </Select>
      <Select v-show="select1=='2'" v-model="select2" multiple style="width: 310px;float: left">
        <Option value="43" ></Option>
        <Option value="44" ></Option>
        <Option value="45" ></Option>
      </Select>
    </FormItem>
    <FormItem label="">
      <Upload :action="this.axios.defaults.baseURL+'/file/add/'+acctok"
              :format="['txt','zip','doc','docx','xls', 'xlsx', 'pdf', 'html']"
              :on-success="upsuccess"
              ref="upload"

      >
        <Button type="ghost" icon="ios-cloud-upload-outline"></Button>
        txtzipdocdocxxlsxlsxpdf
      </Upload>
    </FormItem>
    <FormItem label="">
      <Button type="primary" @click="send" ></Button>
    </FormItem>
  </Form>
</div>
</template>

<script>
	export default {
		name: "notify",
    data(){
		  return{
        formItem:{
          title:"",
          context:""
        },
        acctok: localStorage.getItem('ACCESSTOKEN'),
        selectv:[],
        select:[
          {label: '', value: 'all' },
          {label: '', value: '1' },
          {label: '', value: '2', children:[
              {label: '', value: 'all' },
              {label: '', value: '43' },
              {label: '', value: '44' },
              {label: '', value: '45' },
            ]},
          {label: '', value: '3' },
        ],
        select1:'',
        select2:[]
      }
    },
    mounted(){
      this.$Message.config({
        top: 300,
      });
    },
    methods:{
      upsuccess(res,file){
        console.log(res)
        if(res.code=='000000'){
          file.url=res.data.docPath
        }else{
          this.$Message.error(res.status)
          this.$refs.upload.fileList.pop()
        }
      },
		  send(){
        // console.log(this.$refs.upload.fileList)
        //
        if(!this.formItem.title) {
          this.$Message.error('');
          return
        }
        if(!this.formItem.context) {
          this.$Message.error('');
          return
        }
        if(!this.select1){
          this.$Message.error('');
          return
        }
        if(this.select1=='2' && this.select2.length==0){
          this.$Message.error('');
          return
        }
        let self= this
        this.$Modal.confirm({
          title: '',
          content: '<p></p>',
          onOk: () => {
            //
            let roleId= this.select1, companyCategory=[]
            if (roleId=='2') companyCategory=this.select2

            //
            let url=[], fileList= this.$refs.upload.fileList
            for (let i in fileList){
              url.push(fileList[i].url)
            }

            //
            let param={
              accessToken: this.acctok,
              title: this.formItem.title,
              content: this.formItem.context,
              roleId: roleId,
              companyCategory: companyCategory,
              url: url
            }
            this.axios({
              method: 'post',
              url: '/message/notify/sendNotice',
              headers: {
                'Content-type': 'application/json'
              },
              data: JSON.stringify(param)
            }).then(function (res) {
              console.log(res)
              if(res.data.code= '000000'){
                self.$Message.success("")
              }else{
                self.$Message.error(res.data.status);
              }
            })
          },
          onCancel: () => {

          }
        });


      }
    }
	}
</script>

<style  lang="scss">
.textarea{
  textarea{
    height: 300px;
  }
}
</style>



