<template>
<div class="map-frame" :class="{high: this.type=='maintain'}">
<div class="left">
  <Select v-model="typeV" placeholder="" @on-change="changeSelectAll" class="type" v-if="this.type=='maintain'">
    <Option value="164"></Option>
    <Option value="166"></Option>
    <Option value="214"></Option>
    <Option value="215"></Option>
    <Option value="213"></Option>
  </Select>
  <Input v-model="inputV" placeholder="/" :class="{inline: this.type=='maintain'}"
         @on-enter="changeSelect">
    <Button slot="append" icon="ios-search" @click="changeSelect"></Button>
  </Input>
  <div class="select-bar">
    <Select v-model="sortV" placeholder="" clearable @on-change="changeSelect">
      <Option value="1"></Option>
      <Option value="2"></Option>
      <Option value="3"></Option>
    </Select>
    <Select v-model="categoryV" placeholder="" clearable @on-change="changeSelect">
      <Option value="43"></Option>
      <Option value="44"></Option>
      <Option value="45"></Option>
      <Option value="46"></Option>
      <Option value="47"></Option>
    </Select>
    <Select v-model="areaV" placeholder="" clearable @on-change="changeSelect">
      <Option v-for="(item, key) in area" :value="item.areakey" :key="key">{{item.areaname}}</Option>
    </Select>
    <Select v-model="brandV" placeholder="" clearable @on-change="changeSelect" filterable class="brand">
      <Option v-for="(item, key) in brands" :value="item" :key="key"></Option>
    </Select>
  </div>
  <div class="res"><span>{{sum}}</span></div>
  <ul>
    <li class="info" v-for="(item, key) in list" :key="key" @click.stop="openMapInfo(item.corpId)">
      <img :src="item.frontPhoto? item.frontPhoto:'/static/img/nopic.jpg'">
      <div class="list-right">
        <span class="name">{{item.corpName}}</span>
        <span>{{item.corpAdd}}</span>
        <span>{{item.linkTel}}</span>
        <span v-show="item.apart">{{calcApart(item.apart)}}</span>
      </div>
      <div class="appraise" @click.stop="appraise(item.corpId, item.corpName)"></div>
    </li>
  </ul>
  <Page :total="sum" :current="page" :page-size="limit" show-elevator class-name="paging" @on-change="changePage"></Page>
</div>
<div class="right" id="map"></div>
<div class="rates">
  <i @click="closeRates"></i>
  <rate :comname="rateName" :comid="rateId" @success="closeRates"></rate>
</div>
</div>
</template>

<script>
  import Rate from "./Rate";
  import $ from 'jquery'
  export default {
    components: {Rate},
    props:['type','tolimit'],
    data(){
      return{
        typeV:"164",
        inputV:"",
        sortV: "",
        categoryV:"",
        areaV:"",
        brandV:"",
        brands:['','','','AC Schnitzer','','ALPINA','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','DS','','','','','','','','','','','','','GMC','','','','','','','','','','','','','','','','','','Icona','','','Jeep','','','','','','','','','','','','','KTM','','','','','','','','','','','','','','','','','','Lorinser','','','LOCAL MOTORS','','','MINI','','','','','','','','','','','','','','','','','','','','smart','','','','','','','','','','','','','SWM','','','','','','','','','','WEY','','','','','','','','','','','','','','','','',''],
        sum: 0,
        area:[
          {areakey: '310000', areaname: ''},
          {areakey: '310115', areaname: ''},
          {areakey: '310112', areaname: ''},
          {areakey: '310113', areaname: ''},
          {areakey: '310116', areaname: ''},
          {areakey: '310114', areaname: ''},
          {areakey: '310118', areaname: ''},
          {areakey: '310230', areaname: ''},
          {areakey: '310117', areaname: ''},
          {areakey: '310120', areaname: ''},
          ],
        list:[],
        limit: this.tolimit,
        page: 1,
        points:{},
        map: null,
        icon:null,
        rateName:"",
        rateId: ""
      }
    },
    watch:{
      $route:function (val) {
        // console.log(val.query.type)
        switch (val.query.type){
          case 'maintain': this.typeV= '164';break;
          case 'check': this.typeV= '166';break;
          case 'danger': this.typeV= '214';break;
          case 'newenergy': this.typeV= '215';break;
          case 'rescue': this.typeV= '213';break;
        }
      }
    },
    beforeMount(){
      switch (this.$route.query.type){
        case 'maintain': this.typeV= '164';break;
        case 'check': this.typeV= '166';break;
        case 'danger': this.typeV= '214';break;
        case 'newenergy': this.typeV= '215';break;
        case 'rescue': this.typeV= '213';break;
      }
    },
    mounted(){
      this.map = new BMap.Map("map");
      let point = new BMap.Point(121.480201, 31.236336);// 
      this.map.centerAndZoom(point, 13);
      let top_left_control = new BMap.ScaleControl({
        anchor : BMAP_ANCHOR_BOTTOM_RIGHT
      });//
      let top_left_navigation = new BMap.NavigationControl({offset: new BMap.Size(20, 20)}); // 
      this.icon=new BMap.Icon("static/img/point-icon.png", new BMap.Size(32, 32));//
      this.icon.imageSize= new BMap.Size(32, 32)

      this.map.addControl(top_left_control);
      this.map.addControl(top_left_navigation);

      let systok= localStorage.getItem("SYSTEMTOKEN"),self= this
      if(systok){
        // this.getArea(systok)
        this.getList(systok)
        this.getPoint(systok)
      }else{
        let data = {
          device: uuid(),
          password: "k5pg8kkN",
          username: "pcqixiu"
        }
        this.axios({
          method: 'post',
          url: '/system/terminalsystem/login',
          headers: {
            'Content-type': 'application/json'
          },
          data: JSON.stringify(data)
        }).then(function (response) {
          let systok= response.data.data.systemToken
          localStorage.setItem("SYSTEMTOKEN", systok)
          // self.getArea(systok)
          self.getList(systok)
          self.getPoint(systok)
        })
      }

      function  uuid() {
        var s = [];
        var hexDigits = "0123456789abcdef";
        for (var i = 0; i < 32; i++) {
          s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
        }
        s[14] = "4";  // bits 12-15 of the time_hi_and_version field to 0010
        s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1);  // bits 6-7 of the clock_seq_hi_and_reserved to 01
        // s[8] = s[13] = s[18] = s[23] = "-";

        var uuid = s.join("");
        return uuid;
      }
    },
    methods:{
      getArea(systok){
        let self=this
        this.axios({
          method: 'get',
          url: '/area/county/'+ systok+'/310100'
        }).then(function (res) {
          // console.log(res)
          self.area= res.data.data
        })
      },
      getList(systok){
        let self= this,
          param = {
          companycategory: this.categoryV,
          systemToken: systok? systok: localStorage.getItem("SYSTEMTOKEN"),
          corpAreaEq: this.areaV,
          corpName: this.inputV,
          magorBrandsLk: this.brandV,
          isort: parseInt(this.sortV),
          starLevel:'',
          type: this.typeV,
          limit: this.limit,
          page: this.page
        }
        this.axios({
          method: 'post',
          url: '/maintain/getRangeCorps',
          headers: {
            'Content-type': 'application/json'
          },
          data: JSON.stringify(param)
        }).then(function (res) {
          // console.log(res.data)
          let datas= res.data.data.content
          self.list= [];
          self.list= datas;
          self.sum= res.data.data.totalElements
          for (let i in datas){
            if(!self.points.hasOwnProperty( datas[i].corpId)){
              self.point(datas[i])
            }
          }
        })
      },
      getPoint(systok){
        let self= this,
          param = {
            systemToken: systok? systok: localStorage.getItem("SYSTEMTOKEN"),
            type: this.typeV,
            limit: 200,
            page: 1
          }
        this.axios({
          method: 'post',
          url: '/maintain/getRangeCorps',
          headers: {
            'Content-type': 'application/json'
          },
          data: JSON.stringify(param)
        }).then(function (res) {
          console.log(res.data)
          let datas= res.data.data.content
          for (let i in datas){
            let corp= datas[i]
            self.point(corp)

          }
        })
      },
      point(corp){
        let self=this, point= new BMap.Point(corp.lng, corp.lat);
        let mar= new BMap.Marker(point, {icon: self.icon})
        let html=
          '<div class="info">' +
          '<div class="img">' +
          '<img src='+ (corp.frontPhoto? corp.frontPhoto: "/static/img/nopic.jpg") +' width="170" height="140" />' +
          '</div>' +
          '<ul>' +
          '<li><span></span>' + (corp.corpName?corp.corpName:"") + '</li>' +
          '<li><span></span>' + (corp.companybusinessscope? corp.companybusinessscope: '') + '</li>' +
          // '<li><span></span>' + getStar(corp.STAR_LEVEL) + '</li>' +
          '<li><span></span>' + (corp.creditLevel?corp.creditLevel:"" )+ '</li>' +
          '<li><span></span>' + (corp.vehicleband!=""?corp.vehicleband: "") + '</li>' +
          '<li><span></span>' + (corp.time?corp.time:"" )+ '</li>' +
          '<li><span></span>' + (corp.corpAdd? corp.corpAdd: "") + '</li>' +
          // '<li><span></span>' + (corp.linkTel?corp.linkTel:"") + '</li>' +
          '<li><span></span>' + '******' + '</li>' +
          '<li><span></span></li>' +
          '</ul>' +
          '</div>' +
          '<div class="button">' +
          '<a class=""  href="/#/service/visitingService" ></a>' +
          '<a class=""  href="/#/service/reservationFix" ></a>' +
          '<a class="blue"  href="/#/maintainDetail?id=' + corp.corpId + '"></a>' +

          '</div>';
        mar.searchInfoWindow =new BMapLib.SearchInfoWindow(self.map, html, {
          title: '<b>' + corp.corpName + '</b>', // 
          width: 500, // 
          panel: "panel", // 
          enableAutoPan: true, // 
          enableSendToPhone: false,
          //enableMessage : false,
          searchTypes: [
            BMAPLIB_TAB_FROM_HERE,// 
            BMAPLIB_TAB_SEARCH, // 
            BMAPLIB_TAB_TO_HERE // 
          ]
        });
        mar.addEventListener("click", function () {
          this.searchInfoWindow.open(mar);
        });
        this.map.addOverlay(mar);
        this.points[corp.corpId]= mar
      },
      calcApart(ap){
        let flap= parseFloat(ap)
        if(flap>=1000){
          return (flap/1000).toFixed(1)+' km'
        }else{
          return ap+' '
        }
      },
      openMapInfo(id){
        this.points[id].searchInfoWindow.open(this.points[id])
      },
      changePage(page){
        this.page= page
        this.getList()
      },
      changeSelect(){
        this.page=1
        this.getList()
      },
      changeSelectAll(val){
        this.page=1
        this.map.clearOverlays();
        this.getList()
        if(val=='164'){
          this.getPoint()
        }
      },
      appraise(id,name){
        let acctok= localStorage.getItem("ACCESSTOKEN"), self= this ;
        if(!acctok) {
          this.$Message.error({content:''})
          return
        }
        this.rateId= id
        this.rateName= name
        $(".rates").show()
      },
      closeRates(){
        $(".rates").hide()
      }
    },
    beforeDestroy(){
      this.map= null
      $("#map").remove()
    },
    destroyed() {
      // this.map.reset();
      // this.map= null
      // delete this.map
      // this.$destroy()
      // window.location.reload()
      // this.$router.go(0)
    },
  }
</script>

<style scoped lang="scss">
.map-frame{
  width: 100%;
  /*padding: 10px;*/
  overflow: hidden;
  position: relative;
  min-width: 800px;
  max-width: 1200px;
  display: inline-block;
  text-align: left;
  box-sizing: border-box;
  border: 1px solid #e6e6e6;
  .left{
    float: left;
    position: relative;
    padding: 5px;
    background: #FFF;
    z-index: 888;
    width: 380px;
    .select-bar{
      display: flex;
      margin-top: 10px;
      .ivu-select{
        width: 25%;
        padding-right: 5px;
      }

    }
    .res{
      text-align: center;
      height: 40px;
      line-height: 40px;
      span{
        color: red;
      }
    }
    ul{
      li.info:hover{
        border: 1px solid #1E9FFF;
      }
      li.info{
        overflow: hidden;
        border: 1px solid #ededed;
        margin-bottom: 5px;
        position: relative;
        img{
          float: left;
          margin: 8px;
          width: 100px;
          height: 70px;
        }
        .list-right{
          float: left;
          width: 250px;
          margin-top: 9px;
          font-size: 12px;
          line-height: 18px;
          position: relative;
          span{
            /*color: #020202;*/
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            display: block;
            cursor: default;
          }
          .name{
            background: url(../assets/map/house.png) no-repeat left center;
            color: #252525;
            padding-left: 20px;
            background-size: 15px;
          }
        }
        .appraise{
          position: absolute;
          right: 10px;
          bottom: 10px;
          background-color: white;
          color: #1E9FFF;
          border: 1px solid #1E9FFF;
          font-size: 12px;
          padding: 2px 10px;
          border-radius: 2px;
          cursor: pointer;
        }
      }
    }
  }
  .right{
    height: 600px;
    border-left: 1px solid #e6e6e6;
    border-left: 0;
    position: relative;
    margin-left: 380px;
    min-width: 400px;
  }
  .rates{
    width: 500px;
    height: 450px;
    position: absolute;
    margin: auto;
    background-color: white;
    border-radius: 10px;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 890;
    border:  1px solid #e6e6e6;
    display: none;
    i{
      font-size: 20px;
      width: 500px;
      text-align: right;
      display: block;
      padding-right: 10px;
      line-height: 20px;
      height: 25px;
      border-bottom:  1px solid #e6e6e6;
      cursor: pointer;
    }
  }
}
.map-frame.high{
  .left{
    .type{
      width: 30%;
      padding-right: 5px;
    }
    .inline{
      width: 69%;
      display: inline-table;
    }
  }
  .right{
    height: 800px;
  }
}
</style>
<style lang="scss">
.paging {
  text-align: center;
  li{
    margin: 2px 0;
  }
}
.map-frame .brand .ivu-select-dropdown{
  overflow-x: hidden;
}
.map-frame .right{
  .info{
    padding: 10px;
    .img{
      float: right;
      width: 170px;
      height: 140px;
      img{
        width: 100%;
      }
    }
    ul li{
      line-height: 30px;
    }
  }
  .button{
    padding: 10px;
    text-align: right;
    a{
      display: inline-block;
      height: 35px;
      line-height: 35px;
      color: #1E9FFF;
      border: 1px solid #1E9FFF;
      font-size: 14px;
      border-radius: 2px;
      cursor: pointer;
      padding: 0 10px;
      margin-left: 10px;
    }
    .blue{
      background-color: #1e9fff;
      color: white;
    }
  }
}
.map-frame .right .BMapLib_SearchInfoWindow table td:first-child{
  width: 40px;
}
.map-frame .right .BMapLib_SearchInfoWindow table td:last-child{
  width: 120px;
}
</style>



