<template>
<div class="my-frame">
  <sub-title title="" :link="[{name:'',to: ''},{name:'',to:''}]"></sub-title>
<div class="content">
  <Form :model="formItem" :label-width="150">
    <Row>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Input v-model="formItem.companyname" placeholder="companyname"></Input>
      </Form-item>
      </Col>
      <!--<Col :md="10" :sm="12" :lg="12">-->
      <!--<Form-item label="">-->
      <!--<Input v-model="formItem.companypassword" placeholder="companypassword"></Input>-->
      <!--</Form-item>-->
      <!--</Col>-->
    </Row>

    <Row>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Input v-model="formItem.companyroadtransportationlicense" placeholder="companyroadtransportationlicense"></Input>
      </Form-item>
      </Col>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Input v-model="formItem.companyunifiedsocialcreditidentifier" placeholder="companyunifiedsocialcreditidentifier"></Input>
      </Form-item>
      </Col>
    </Row>

    <Row>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="()">
        <Input v-model="formItem.companyaddress.address" placeholder="companyaddress"></Input>
      </Form-item>
      </Col>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Input v-model="formItem.companypostalcode" placeholder="companypostalcode"></Input>
      </Form-item>
      </Col>
    </Row>

    <Row>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Select v-model="formItem.companyeconomiccategory" placeholder="">
          <Option v-for="(item, key ) in economicCategory" :value="key" :key="item">{{item}}</Option>

        </Select>
      </Form-item>
      </Col>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Select v-model="formItem.companycategory" placeholder="">
          <Option v-for="(item, key ) in category" :value="key" :key="item">{{item}}</Option>

        </Select>
      </Form-item>
      </Col>
    </Row>

    <Row>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Input v-model="formItem.companylinkmanname" placeholder="companylinkmanname"></Input>
      </Form-item>
      </Col>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Input v-model="formItem.companylinkmantel" placeholder="companylinkmantel"></Input>
      </Form-item>
      </Col>
    </Row>

    <Row>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Input v-model="formItem.companysuperintendentname" placeholder="companysuperintendentname"></Input>
      </Form-item>
      </Col>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Input v-model="formItem.companysuperintendenttel" placeholder="companysuperintendenttel"></Input>
      </Form-item>
      </Col>
    </Row>

    <Row>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Input v-model="formItem.companybusinessscope" placeholder="companysuperintendentname"></Input>
      </Form-item>
      </Col>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Input v-model="formItem.roadtransportationlicensestartdate" placeholder=":20100101"></Input>
      </Form-item>
      </Col>
    </Row>

    <Row>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Input v-model="formItem.roadtransportationlicenseenddate" placeholder=":20100101"></Input>
      </Form-item>
      </Col>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Select v-model="formItem.companyoperationstate" placeholder="">
          <Option v-for="(item, key ) in operationState" :value="key" :key="item">{{item}}</Option>

        </Select>
      </Form-item>
      </Col>
    </Row>

    <Row>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Select placeholder="" class="area" v-model="formItem.companyadministrativedivisioncode">
          <Option v-for="(item, key ) in area3" :value="item.areakey" :key="key">{{item.areaname}}</Option>
        </Select>

      </Form-item>
      </Col>
      <Col :md="10" :sm="12" :lg="12">
      <Form-item label="">
        <Input v-model="formItem.companyemail" placeholder="companyemail"></Input>
      </Form-item>
      </Col>
    </Row>

    <Form-item>
      <Button type="primary" @click="handleSubmit('formDynamic')"></Button>
      <!--<Button type="ghost" @click="handleReset('formDynamic')" style="margin-left: 8px"></Button>-->
    </Form-item>
  </Form>
</div>
</div>
</template>

<script>
	export default {
		name: "complete-info",
    data(){
      return{
        formItem: {
          accessToken: localStorage.getItem('ACCESSTOKEN'),
          companyname: '',
          companypassword: this.calcPass(),
          companyroadtransportationlicense:"31" +
          "",
          companyunifiedsocialcreditidentifier:"",
          companyaddress:{
            address: ''
          },
          companypostalcode:"",
          companyeconomiccategory:"",
          companycategory:"",
          companylinkmanname:"",
          companylinkmantel:"",
          companysuperintendentname:"",
          companysuperintendenttel:"",
          companybusinessscope:"",
          roadtransportationlicensestartdate:"",
          roadtransportationlicenseenddate:"",
          companyoperationstate:"",
          companyadministrativedivisioncode:"",
          companyemail:"",

        },
        economicCategory:{"100":"","110":"","120":"","130":"","140":"","141":"","142":"","143":"","149":"","150":"()","159":"()","160":"()","170":"","171":"","172":"","173":"()","174":"()","175":"","179":"","190":"","200":"","210":"","220":"","230":"","240":"()","290":"","300":"","310":"","320":"","330":"","340":"()","390":"","900":""},
        category:{"01":"","02":"","03":""},
        operationState:{"1":"","2":"","3":"","4":"","5":"","6":"","9":""},
        area3:[],

      }
    },

    methods:{
      handleSubmit(){
        let self= this;
        for(let key in this.formItem){
          if(!this.formItem[key]){
//				        console.log(this.formItem[key])
            this.$Message.error('!');
            return
          }
        }
        this.axios({
          method: 'post',
          url: '/company/repaircompany/edit',
          headers: {'Content-type': 'application/json'},
          data: JSON.stringify(this.formItem)
        }).then(function (response) {
          console.log(response);
          if(response.data.code!='000000'){
            self.$Modal.error({
              title: "",
              content: response.data.status
            })
          }else{
            self.$Modal.success({
              title: "",
              content: ""
            })
          }

        }).catch(function (response) {
          self.$Modal.error({
            title: "",
            content: response.response.data? response.response.data.status: ""
          })
          console.log(response);
        });

      },
      calcPass(){
        var len = 12;
        var $chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678';    /****oOLl,9gq,Vv,Uu,I1****/
        var maxPos = $chars.length;
        var pwd = '';
        for (var i = 0; i < len; i++) {
          pwd += $chars.charAt(Math.floor(Math.random() * maxPos));
        }
        return pwd;
      }
    },
    mounted(){
      var self = this
      this.axios({
        method: 'get',
        url: '/area/county/'+ localStorage.getItem("ACCESSTOKEN")+ '/310100',
      }).then(function (response) {
        if(response.data.code=='0'){
          console.log(response.data)
        }
        let res = response.data.data
        for (let i in res){
          self.area3.push(res[i])
        }
      })
    },
	}
</script>

<style scoped>
.content{
  background-color: #eff0f4;
}
</style>



